import React, { useEffect } from "react";
import "../../App.css";

import Nav from "../../components/Nav";
import FloatingChat from "../../components/FloatingChat";
import BandeauCookie from "../../components/BandeauCookie";

function PolitiqueConfidentialite() {
  useEffect(() => {
    document.getElementsByClassName("sidenav-overlay")[0].style.opacity = "0";
    document.title = "O'Films | Politique de confidentialit";
  });
  return (
    <>
      <Nav />
      <div className="container" style={{ textAlign: "justify" }}>
        <h2>Politique de confidentialit</h2>
        <p style={{ fontWeight: "bold" }}>
          Dernire mise  jour : 10 octobre 2019
        </p>
        <p>
          L'quipe du site O'FILMS est soucieuse de la protection des donnes
          personnelles. Elle s'engage  assurer le meilleur niveau de protection
           vos donnes personnelles en conformit avec les rglementations
          europennes et franaises qui lui sont applicables en matire de
          protection des donnes personnelles. Pour toute information sur la
          protection des donnes personnelles, vous pouvez galement consulter
          le site de la Commission Informatique et Libert www.cnil.fr.
        </p>
        <p>
          Les donnes personnelles collectes sont les suivantes : civilit,
          nom, prnom, adresse email, dpartement, ville, numro de tlphone,
          pseudo, mot de passe. Ces donnes sont collectes et stockes 
          l'inscription, selon les champs remplis par l'utilisateur.
        </p>
        <p>
          Les donnes ainsi collectes sur le Site Internet sont exclusivement
          destines  l'usage propre de O'FILMS. Elles peuvent tre transmises
          aux autres sites de l'quipe O'FILMS agissant sous l'enseigne O'FILMS.
          Elles peuvent galement tre transmises aux personnes agissant sous
          l'autorit et sur les instructions de O'FILMS auxquelles O'FILMS fait
          appel.
        </p>
        <p>
          Aucune des donnes personnelles collectes  partir du Site Internet
          n'est communique ou cde  des tiers  des fins commerciales.
        </p>
        <p>
          Pour toute question  propos de la confidentialit et de la protection
          de vos donnes, veuillez nous contacter par le formulaire de contact
          prsent{" "}
          <a href="/contact" style={{ textDecoration: "underline" }}>
            ici
          </a>
          .
        </p>
      </div>
      <FloatingChat />
      <BandeauCookie />
    </>
  );
}

export default PolitiqueConfidentialite;
